.isPlaying{
  background: #2b231b;
  opacity: 0.75;
  margin: 15px;
  padding: 10px;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s
}

.btns{
  margin-top: 10px;
  margin-left: 10px;
  width: 20%;
}

.bg{
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(38, 118, 114)), color-stop(85%, rgba(19, 18, 18, 0.919)));
  background-image: -webkit-linear-gradient(rgb(38, 118, 114), rgba(19, 18, 18, 0.919) 85%);
  background-image: -o-linear-gradient(rgb(38, 118, 114), rgba(19, 18, 18, 0.919) 85%);
  background-image: linear-gradient(rgb(38, 118, 114), rgba(19, 18, 18, 0.919) 85%);
  width: 100%;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  left: 0;
  top: 0;
  position: fixed;
  will-change: transform;
  color: white;
  padding-top: 80px;
}

.song{
  margin: 15px;
  padding: 10px;
}

.list {
  background: rgba(136, 134, 134, 0.309);

  border-radius: 25px;
}

.title{
  background-color: rgb(38, 118, 114);
  border-radius: inherit;
}

.btnsList{
  background-color: rgb(38, 118, 114);
  border-radius: inherit;
  padding-bottom: 15px;
}


